.panel-row:nth-child(odd) > .pixel:nth-child(odd){
   background: #D9D9D9;
}
.panel-row:nth-child(even) > .pixel:nth-child(even){
   background: #D9D9D9;
}
 */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.cover .row{
    height: 100vh;
    width: 100%;
    align-items: center;
    overflow: hidden;
}
.col-md-7,.col-md-5{
    padding: 0;
}
.col-md-5 .text-area{
    margin-left: 70px ;
}
.col-md-5 h2{
    font-size: 2em;
}
.col-md-5 h4{
    font-size: 1.5em;
    font-weight: 300;
    margin: 15px 0 10px;
    line-height: 25px;
}
.col-md-5 p{
    font-size: 1em;
    font-weight: 200;
    margin-top: 10px; 
}
.col-md-5 .text-area::after{
    content: "";
    width: 10px;
    height: 60%;
    background: #00ddff;
    left: 40px;
    top: 8px;
    position: absolute;
}
.art-area{
    display: flex;
    justify-content: center
}
.art-area img{
    width: 70%;
    align-self: center;
    z-index: 2;
    -webkit-filter: drop-shadow(0.35rem 0.35rem 0.4rem rgba(0, 0, 0, 0.5));
            filter: drop-shadow(0.35rem 0.35rem 0.4rem rgba(0, 0, 0, 0.5));
} 
.art-area svg{
    position: absolute;
    z-index: 1;
    height: 100%;
    right: -100px;
}
.start-btn{
    background-color:#00ddff;
    border-color: bisque;
    height: 60px;
    line-height: 45px;
    width: 200px;
    border-radius: 20px;
    font-size: 1.5em;
    font-weight: 600;
}
* {
    padding: 0;
    margin: 0;
    border: 0;
	font-size: 100%;
    font-weight: normal;

}
